import { ImgContainer } from "../cmps/img-container"
import homepageImg from "../assets/img/toystore.jpg"
import { Link } from 'react-router-dom'

export const Home = () => {

    return (
        <section className="homepage">
            <ImgContainer
                src={homepageImg}
                name={'homepage-img'}
                width="100%"
                divStyle={"image-homepage"} />
            <div className="main-layout homepage-container">
                <h1>Welcome to our toy store</h1>
                <span>
                    <Link to={`/toy`} className="toy-link">Click here to see our toys!</Link>
                </span>
            </div>
        </section>
    )
}