import React from "react"
import GoogleMapReact from 'google-map-react'
import { useState } from "react"

const AnyReactComponent = ({ text }) => <div>{text}</div>

export function GoogleMap() {
    
  const [coordinates, setCoordinates] = useState({ lat: 32.0853, lng: 34.7818 })
  const zoom = 11

  const onClick = ({ x, y, lat, lng, event }) => {
    console.log(x, y, lat, lng, event)
    setCoordinates({lat, lng})
  }

  return (
    // Important! Always set the container height explicitly
    <div className="google-map">
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyAB1IQp6hqgWqW3i2SmAf-l8sar-clzaEs" }}
        defaultCenter={coordinates}
        center={coordinates}
        defaultZoom={zoom}
      >
        <AnyReactComponent
          lat={coordinates.lat}
          lng={coordinates.lng}
          text="🍓"
        />
      </GoogleMapReact>
      <br/>
      <section className="branches">
        <button onClick={()=>onClick({ lat: 32.0853, lng: 34.7818 })}>Tel Aviv</button>
        <button onClick={()=>onClick({ lat: 32.4340, lng: 34.9197 })}>Hadera</button>
        <button onClick={()=>onClick({ lat: 32.0132, lng: 34.7480 })}>Bat Yam</button>
        </section>
    </div>
  )
}