import { useEffect, useState } from 'react'
import { useRef } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useForm } from '../hooks/useForm'
import { toyService } from '../services/toy.service'
import { addToy, updateToy } from '../store/actions/toy.action'
import { useDispatch } from 'react-redux'
import { MultiSelect } from "react-multi-select-component"

export const ToyEdit = (props) => {
    const inputRef = useRef()

    // For MultiSelect
    const options = [
        { label: "On wheels 🛴", value: "On wheels 🛴" },
        { label: "Box game 🎁", value: "Box game 🎁" },
        { label: "Art 🍓", value: "Art 🍓" },
        { label: "Baby 👶", value: "Baby 👶" },
        { label: "Doll 🧸", value: "Doll 🧸" },
        { label: "Puzzle 🧩", value: "Puzzle 🧩" },
        { label: "Outdoor 🤸‍♂️", value: "Outdoor 🤸‍♂️" },
    ]

    const params = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()

    const [toy, handleChange, setToy] = useForm({
        name: '',
        price: '',
        label: [],
        inStock: false
    })

    useEffect(() => {
        inputRef.current.focus()
        const toyId = params.id
        if (!toyId) return
        toyService.getById(toyId)
            .then(toy => {
                setToy(toy)
            })
            .catch(err => {
                console.log('err:', err);
            })
    }, [])


    const onSaveToy = (ev) => {
        ev.preventDefault()
        if (toy._id) {
            dispatch(updateToy(toy))
        } else {
            dispatch(addToy(toy))
        }
        navigate('/toy')
    }

    function handleSetSelected(items) {
        const newToy = { ...toy, label: [...items.map(item => item.label)] }
        setToy(newToy)
    }

    return (
        <section className='toy-edit'>
            <h1>{toy._id ? 'Edit' : 'Add'} Toy</h1>
            <pre>{JSON.stringify(toy, null, 2)}</pre>
            <form onSubmit={onSaveToy}>
                <label htmlFor="name">Name</label>
                <input ref={inputRef} value={toy.name} onChange={handleChange} type="text" name="name" id="name" />
                <label htmlFor="price">Price</label>
                <input ref={inputRef} value={toy.price} onChange={handleChange} type="number" name="price" id="price" />
                <div>
                    {/* <pre>{JSON.stringify(selected, null, 2)}</pre> */}
                    <MultiSelect
                        name="label"
                        id="label"
                        type="text"
                        options={options}
                        value={toy.label.map(item => ({ label: item, value: item }))}
                        onChange={handleSetSelected}
                        labelledBy="Select"
                    />
                </div>
                <label htmlFor="stock">Stock</label>
                <select value={toy.inStock} onChange={handleChange} type="text" name="inStock" id="stock">
                    <option value={true}>Yes</option>
                    <option value={false}>No</option>
                </select>
                <button>Save</button>
            </form>
        </section>
    )
}
