import React from 'react'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js'
import { Doughnut } from 'react-chartjs-2'
import { useDispatch, useSelector } from 'react-redux'
import { useEffect } from 'react'
import { loadToys } from '../store/actions/toy.action'




ChartJS.register(ArcElement, Tooltip, Legend)

export function ToyChart() {

  const { toys } = useSelector(state => state.toyModule)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(loadToys())
  }, [])


  function countLabels(items) {
    const labels = {}
    let toysInStock = items?.filter(item => item.inStock)
    toysInStock?.forEach(toy => {
      toy.label.forEach(label => {
        labels[label] = labels[label] ? labels[label] + 1 : 1
      })
    })
    return labels
  }

  const countedLabels = countLabels(toys)
  const percentageLabels = gerPercentageByLabel(countedLabels)
  
  function gerPercentageByLabel(countedLabels) {
    let valLabels = Object.values(countedLabels)
    return valLabels.map(valLabel => Math.floor(valLabel * 100 / valLabels.length))
  }
  const data = {
    labels: Object.keys(countedLabels),
    datasets: [
      {
        data: percentageLabels,
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
          'rgba(150, 100, 150, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
          'rgba(150, 100, 150, 1)',
        ],
        borderWidth: 3,
      },
    ],
  }

  return <div className="doughnut main-layout">
    <div className="chart">
      <Doughnut data={data} />
    </div>
  </div>
}
